<template>
  <div class="wrapper">
    <typeChange @change="changeType"></typeChange>
    <div class="content">
      <div class="c-top">
        <div class="ct-left">
          <ctitle :title="'信访量四级登记机构占比'"></ctitle>
          <div class="inner">
            <div class="part part1">
              <allCount :count="allcount"></allCount>
            </div>
            <div class="part part2">
              <detailList :params="detailList"></detailList>
            </div>
            <div class="part part3">
              <funnel :params="zbFunnelParams"></funnel>
            </div>
          </div>
        </div>
        <div class="ct-right">
          <ctitle :title="'信访量四级登记机构占比走势'"></ctitle>
          <div class="inner">
            <timeChange class="timeChange" :key="curTypeId" @change="zsTimeClick"></timeChange>
            <PerBar :params="zsBarParams"></PerBar>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <ctitle :title="'信访量四级登记机构-机构类别占比'"></ctitle>
        <div class="inner">
          <div class="chart" v-for="(item,index) of djjgArr" :key="index">
            <loopPie :params="item.params"></loopPie>
            <p class="label">{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import typeChange from "@/components/typeChange/index";
  import timeChange from "@/components/timeChange/index";
  import incNumber from "@/components/incNumber/index";
  import funnel from "@/components/funnel/Funnel";
  import PerBar from "@/components/bar/PerBar";
  import loopPie from "@/components/pie/LoopPie";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";

  export default {
    name: "djqy",
    components: {
      ctitle,
      typeChange,
      timeChange,
      incNumber,
      funnel,
      PerBar,
      loopPie,
      allCount,
      detailList
    },
    data() {
      return {
        curTypeId: 'drop',
        // 登记机构占比
        allcount: -1,
        detailList: null,
        zbFunnelParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            tooltipShow: false,
            sort: "none",
            top: "10%",
            borderTop: "11%",
            rollFlag: true
          }
        },
        // 登记机构走势
        zsBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            mode: "stack",
            legendShow: true,
            legendPos: {
              //图例位置
              right: "10%",
              top: "8%"
            },
            legendSize: 14, // label字号
            legendIcon: "rect",
            legendW: 12,
            legendH: 12,
            legendColor: "#fff",
            xName: "name", //x轴字段
            axisCategoryName: "年",
            axisValueShow: true,
            barLabelShow: false,
            perBar: true,
            xsplitLineShow: false,
            left: "12%",
            right: "10%",
            top: "15%",
            bottom: "5%",
            axisLabelSize: "14%", // label字号
            colors: {
              bar: this.$STANDARD.gradientColors,
            },
            map: {}
          }
        },
        //登记机构占比
        djjgArr: [
          {name: '国家', params: {data: [],options:{}}},
          {name: '省级', params: {data: [],options:{}}},
          {name: '市级', params: {data: [],options:{}}},
          {name: '县级', params: {data: [],options:{}}}
        ],
        djjgPieParams: {
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["58%", "78%"],
            center: ["50%", "50%"],
            centerRadius: ["45%", "46%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.18),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.year, this.curTypeId);
    },
    methods: {
      // 头部类型切换
      changeType(id) {
        this.curTypeId = id;
        this.getAPI(this.year, this.curTypeId);
      },
      getAPI(year, id) {
        // 1.登记机构占比（总数）
        get_http("zlfxDjjgZbtotal", {year: year, xfxs: id})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.登记机构占比（各类型）
        get_http("zlfxDjjgZbType", {year: year, xfxs: id})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.登记机构占比（漏斗图）
        get_http("zlfxDjjgZbFunnel", {year: year, xfxs: id})
          .then(res => {
            this.zbFunnelParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4登记机构走势
        this.getZsLineAPI(year, id, '年度');
        // 5四级登记机构-机构类别占比（饼图）
        this.getDjjgZbPieAPI(year, id);
      },
      // 四级登记机构-机构类别占比（饼图）
      getDjjgZbPieAPI(year, id) {
        for (let item of this.djjgArr.values()) {
          item.params.options = this.djjgPieParams.options;
          get_http("zlfxDjjgJgPie", {year: year, djjg: item.name, xfxs: id})
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      // 登记机构走势
      getZsLineAPI(year, id, time) {
        let timetype = '', _year = '';

        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("zlfxDjjgZsBar", {
          year: _year,
          timetype: timetype,
          xfxs: id
        })
          .then(res => {
            this.zsBarParams.options.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsBarParams.options.map = res.data.map;
            this.zsBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 登记机构走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, this.curTypeId,time);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val, this.curTypeId);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      flex-direction column
      padding .2rem .3rem
      box-sizing border-box

      .c-top
        flex 1
        display flex

        .ct-left
          flex 1.5
          height 100%
          position relative
          .inner
            display flex
            width 100%
            height calc(100% - 0.28rem)
            .part
              flex 1.5
              display flex
              justify-content center
              align-items center
              &.part1
                flex 1
                margin-left .3rem
            .detail
              flex 1
              box-sizing border-box
              display flex
              justify-content space-evenly
              align-items center
            .chart
              flex 1
              width 100%
              height 100%
        .ct-right
          flex 1
          height 100%
          position relative

          .timeChange
            top 0
            right 1rem
          .inner
            width 100%
            height calc(100% - 0.28rem)
      .c-bottom
        flex 1
        background url('../../../assets/c-bottom-bg.png') no-repeat center
        background-size 100% 100%
        position relative
        box-sizing border-box
        padding .4rem .3rem 0 .3rem
        margin 0 .2rem .1rem .2rem
        .inner
          display flex
          width 100%
          height calc(100% - 0.7rem)
          .chart
            flex 1
            .label
              text-align center
              color #fff
              font-weight 700
              font-size 0.2rem
</style>
