<!--
  @auth:yw
  echarts柱图折线图
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from "echarts";
  import {gradientColor} from "@/utils/gradient";

  export default {
    name: "barline",
    props: {
      animateFlag: Boolean,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "1000px",
              height: "600px"
            },

            /*
             *没有写默认值的都为false
             *带***为必填
             * */
            options: {
              mode: "abreast", // 柱图模式  abreast(并排) | overlap(重叠) | stack(堆叠)  默认并排 ***
              overlap: "investigators", // ***如果mode为'abreast' (重叠) 此项为必填 值为重叠在底部数据的key值
              legendBgSrc: {
                // ***如果mode为'abreast' (重叠) 此项为必填
                // 值为 #激活#和 #未激活# 的背景图片的图片名(图片需为png格式,路径需在assets下)
                // 例: avtive.png 取active
                active: "legendActive",
                inActive: "legendInactive"
              },
              colors: {
                //颜色  STANDARD对象后的值按图表的需求给,详情请看standard.js,
                //如果值为字符串请放入数组内
                bar: this.$STANDARD.gradientColors,
                line: this.$STANDARD.gradientColors
              },
              itemLabelColor: "#fff", //bar label的颜色
              rotate: 20, //x轴旋转角度
              colorisarry: false, //每个柱图选择不同的颜色,
              //colorsbox:[],//颜色数组
              xName: "name", // ***类目轴key值
              isVertical: true, // 类目轴是否垂直
              nameLocation: true, //isVertical为true时,Value轴名称是否在右侧，默认显示
              xsplitLineShow: true, //Value轴splitLine是否显示，默认显示
              axisLabel: true, //isVertical为true时,Value轴label是否显示，默认显示
              axisValuePosition: true, //isVertical为true时,Value轴显示位置
              categoryInverse: true, // isVertical为true时,类目轴是否在右侧
              axisValueShow: true, // value轴 label 和 splitLine是否显示
              axisLineShow: true, // 类目轴底线是否显示
              categoryBr: true, // 类目轴label是否折行
              categoryCut: true, // 类目轴label最后三个字是否折行
              brNum: 2, // 类目轴label每隔多少字符折行 默认2
              barBorderRadius: true, //柱图是否显示圆角，默认不显示
              legendShow: true, // legend是否显示
              legendCut: true, // legend换行
              barLabelShow: true, // bar label是否显示  默认true
              barWidth: 10, // bar label是否隐藏 默认15
              top: "5%", // grid 上下左右值 默认5%
              left: "5%",
              right: "5%",
              bottom: "5%",
              legendPos: {
                //图例位置
                right: "5%",
                top: "5%"
              },
              legendW: 10, //legend宽  默认25
              legendH: 10, //legend高  默认14
              legendGap: 10, //legend间距
              legendIcon: "rect", // legend图标形状
              borderRadius: "50%", //legend的圆角
              map: {
                // ***数据映射
                num: "班子人数",
                age: "平均年龄"
              },
              addTooltip: {
                // legend和图表不显示而tooltip显示的数据
                add_holder: "持证人员"
              },
              isLeftScale: true, // 左侧value轴是否取最小值
              isRightScale: true, // 右侧value轴是否取最小值
              // 以下为 line 才需要的参数
              axis1Name: "（人）", // 左侧value轴name名称 默认''
              axis2Name: "（岁）", // 右侧value轴name名称 默认''
              line: ["age", "num"], // ***line 包含的key值
              isAverage: true, // line 是否需要平均markline
              lineWidth: 2, // line宽度 默认1
              tooltipPercent: true, // tooltip 是否带百分比 (需要数据支持,格式为:值|百分数, 1234|23.32)
              iscolor: true,
              axisCategoryName: "政府部门", // 类目轴name名称 默认''
              smooth: true, // 曲线是否平滑显示
              showSymbol: true, // 曲线是否显示拐点
              lineLabelShow: false, //曲线拐点 label是否隐藏 默认true
              symbolSize: 5, // 曲线拐点的大小
              lineShadowBlur: 20, // 曲线阴影宽度
              lineShadowColor: options.colors.line[1], // 曲线阴影颜色
              lineShadowOffsetY: 15, //曲线阴影位移
              lineOpacity: 0.5, // 曲线阴影透明度
              tooltipType: "shadow", // 选中样式 shadow || line
              axisLabelSize: 20, // label字号
              legendSize: 20 // label字号
            },
            data: [
              {
                department: "汉族",
                value: 6719
              },
              {
                department: "少数民族",
                value: 3345
              }
            ]
          };
        }
      }
    },
    data() {
      return {
        myChart: null,
        legendActive: true, //边框bar legend 状态
        animation: true //是否开启开场动画，切换皮肤时禁用
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;

        let propData = [...this.params.data]; //深拷贝数据
        let categoryData = []; //刻度值

        let options = this.params.options;
        //渐变方向
        let linearGradient = options.isVertical ? [0, 1, 1, 0] : [0, 0, 0, 1];
        // 获取keys数组(series数组)
        let dataKeys = Object.keys(options.map);
        // 是否全为line
        let isAllLines = options.line
          ? options.line.length === dataKeys.length
          : false;
        // 获取key对应的中文数组(legend数组)
        this.legendData = Object.values(options.map);
        //如果为重叠柱图需要引入legend背景图片
        if (options.mode === "overlap" && options.legendBgSrc) {
          let activeSrc = options.legendBgSrc.active;
          let inActiveSrc = options.legendBgSrc.inActive;
          this.activeLegendBg = require(`@/assets/${activeSrc}.png`);
          this.inActiveLegendBg = require(`@/assets/${inActiveSrc}.png`);

          this.legendData.forEach((item, index) => {
            if (options.map[options.overlap] === item) {
              this.legendData[index] = {
                name: item,
                icon:
                  this.params.legendActive === undefined
                    ? `image://${this.activeLegendBg}`
                    : this.params.legendActive[item]
                    ? `image://${this.activeLegendBg}`
                    : `image://${this.inActiveLegendBg}`
              };
            }
          });
        }
        let serirsArr = [];
        let average = 0;
        let percentObj = {}; //line百分比
        if (options.tooltipPercent) {
          // 为每个类目设置空数组
          propData.forEach(item => {
            percentObj[item[options.xName]] = [];
          });
        }

        // 为每个类目填充相应的百分比
        if (options.tooltipPercent) {
          propData.forEach((item, index) => {
            //  记录当前类目值
            let name = item[options.xName];
            for (let key in item) {
              //跳过key为name 和 genre
              if (key !== "name" && key !== "genre") {
                percentObj[name].push(item[key].split("|")[1]);
              }
            }
          });
        }
        // line和bar的index
        let lineIndex = 0,
          barIndex = 0,
          stack = 1;
        let data = [];
        propData.forEach((innerItem, innerIndex) => {
          let total = 0;
          let beforeTotal = 0;
          dataKeys.forEach((items, indexs) => {
            total += innerItem[items];
          });
          dataKeys.forEach((items, indexs) => {
            if(total===0){
              data.push(0)
            }else{
              data.push(((innerItem[items] / total) * 100).toFixed(2));
            }
          });
        });

        let size = dataKeys.length;
        let newarr = [];
        for (let i = 0; i < data.length; i = i + size) {
          newarr.push(data.slice(i, i + size));
        }

        let arrTotal = [];
        newarr[0].forEach(el => {
          arrTotal.push([]);
        });
        newarr.forEach((item, index) => {
          let total = 0;
          item.forEach((innerItem, innerIndex) => {
            total += item[innerIndex];
          });
          total = total - item[item.length - 1];
          // item[item.length - 1] = 100 - total;
          // item.splice(item.length - 1, 1, item[item.length - 1]);
        });
        newarr.forEach((item, index) => {
          item.forEach((innerItem, innerIndex) => {
            arrTotal[innerIndex].push(innerItem);
          });
        });

        //循环series数组
        dataKeys.forEach((item, index) => {
          //每个series数据
          let data = [];
          let map = options.map;
          data = arrTotal[index];
          let series = {};
          //判断图表类型
          if (options.line && options.line.includes(item)) {
            //如果line的legend尾部是'比','度','率'为右侧轴
            this.isRightAxis =
              map[item].endsWith("比") ||
              map[item].endsWith("度") ||
              map[item].endsWith("率");
            series = {
              name: map[item],
              type: "line",
              yAxisIndex: isAllLines ? 0 : this.isRightAxis ? 1 : 0, //全为line 取左侧y轴
              label: {
                show: false
              },
              symbolSize: options.symbolSize ? options.symbolSize : 5,
              symbol: "circle",
              showSymbol:
                options.showSymbol === undefined ? false : options.showSymbol, //是否显示折线上的点
              smooth: options.smooth === undefined ? false : options.smooth, // 是否平滑
              // hoverAnimation: false,
              itemStyle: {
                normal: {
                  label: {
                    show:
                      options.lineLabelShow === undefined
                        ? true
                        : options.lineLabelShow,
                    fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                    formatter: this.isRightAxis ? "{c}%" : "{c}"
                  },
                  color:
                    options.colors && options.colors.line
                      ? this.$STANDARD.getColor(
                      options.colors.line[lineIndex],

                      [0, 1, 1, 0],
                      echarts
                      )
                      : ""
                },
                emphasis: {
                  label: {
                    show: true,
                    fontSize: options.axisLabelSize ? options.axisLabelSize : 20
                  },
                  borderColor:
                    options.colors && options.colors.line
                      ? this.$STANDARD.colorToRgba(
                      options.colors.line[lineIndex],
                      0.3
                      )
                      : "rgba(255,255,255,.3)",
                  borderWidth: 10
                }
              },
              lineStyle: {
                // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，相当于在图形包围盒中的百分比，如果 globalCoord 为 `true`，则该四个值是绝对的像素位置
                color:
                  options.colors && options.colors.line
                    ? this.$STANDARD.getColor(
                    options.colors.line[lineIndex++],
                    // linearGradient,
                    [0, 0, 0, 1],
                    echarts
                    )
                    : "",
                width: options.lineWidth ? options.lineWidth : 3,
                shadowBlur: options.lineShadowBlur ? options.lineShadowBlur : 0,
                shadowColor: options.lineShadowColor
                  ? options.lineShadowColor
                  : "none",
                shadowOffsetY: options.lineShadowOffsetY
                  ? options.lineShadowOffsetY
                  : 0,
                opacity: options.lineOpacity ? options.lineOpacity : 1
              },
              //平均线
              markLine: {
                symbolSize: 10,
                label: {
                  normal: {
                    show: true,
                    color: "#fff",
                    fontSize: 20
                  }
                },
                data: [
                  [
                    {
                      symbol: "line",
                      symbolSize: "0",
                      x: options.isAverage ? "6.7%" : "",
                      yAxis: average
                    },
                    {
                      symbol: "line",
                      symbolSize: "0",
                      x: "95%",
                      yAxis: average
                    }
                  ]
                ],
                lineStyle: {
                  normal: {
                    width: 2,
                    type: "solid",
                    color: "#FF507B"
                  }
                }
              },
              data: data
            };
          } else {
            series = {
              name: map[item],
              type: "bar",
              stack: options.mode === "stack" ? stack : stack++,
              yAxisIndex: 0,
              xAxisIndex:
                options.mode === "overlap" && item === options.overlap ? 1 : 0,
              barWidth: options.barWidth ? options.barWidth : "auto",
              //同series柱图间距
              barGap: options.barGaps ? options.barGaps : "50%",
              //不同series之间的间距
              barCategoryGap: "50%",
              label: {
                show:
                  options.barLabelShow === undefined
                    ? true
                    : options.barLabelShow,
                position: options.barLabelOptions
                  ? options.barLabelOptions
                  : options.isVertical
                    ? options.categoryInverse
                      ? "left"
                      : "right"
                    : "",
                fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                textStyle: {
                  color: options.itemLabelColor ? options.itemLabelColor : ""
                }
                // ,
              },
              itemStyle: {
                normal: {
                  barBorderRadius:
                    options.barBorderRadius === undefined
                      ? 0
                      : options.barBorderRadius,
                  color:
                    options.mode !== "overlap" || options.overlap !== item
                      ? options.colors && options.colors.bar
                      ? options.colorisarry
                        ? function (params) {
                          let colorList = options.colors.bar
                            ? options.colors.bar
                            : [
                              "#fa2b7d",
                              "#ff5463",
                              "#ff874a",
                              "#f1825f",
                              "#c64aa7",
                              "#9a23e6",
                              "#7537fb",
                              "#5660fe",
                              "#4ac0ff",
                              "#4ae6ff"
                            ];
                          return colorList[params.dataIndex];
                        }
                        : this.$STANDARD.getColor(
                          options.colors.bar[barIndex++],
                          linearGradient,
                          echarts
                        )
                      : ""
                      : "transparent",
                  borderColor:
                    options.mode === "overlap" && options.overlap === item
                      ? options.colors && options.colors.bar
                      ? this.$STANDARD.getColor(
                        options.colors.bar[barIndex++],
                        linearGradient,
                        echarts
                      )
                      : ""
                      : "transparent",
                  borderWidth: 2
                }
              },
              emphasis: {
                label: {
                  show:
                    options.barLabelShow === undefined
                      ? true
                      : options.barLabelShow,
                  fontSize: options.axisLabelSize ? options.axisLabelSize : 20
                }
              },
              data: data
            };
          }

          serirsArr.push(series);
        });

        //值轴
        let axisValue = [
          {
            show: options.axisValueShow,
            type: "value",
            name: options.axis1Name ? options.axis1Name : "%",
            position:
              options.isVertical && options.axisValuePosition ? "top" : "bottom",
            inverse: options.categoryInverse,
            nameTextStyle: {
              color: "#ff517f",
              fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
              fontWeight:'bold',
              padding: [0, 30, 0,0]
            },
            scale: options.isLeftScale ? true : false,
            splitNumber: options.splitNumber ? options.splitNumber : 5,
            axisTick: {
              show: false
            },
            axisLabel: {
              show: options.isVertical && options.axisLabel ? false : true,
              color: (value, index) => {
                let arr=[];
                let num=options.splitNumber ? options.splitNumber : 5;
                if(index<num){
                  for(let i=0;i<num;i++){
                    arr.push(0)
                  }
                  let gradientC = gradientColor(arr, "#fdc48d", "#ff517f");
                  return gradientC(index)
                }else{
                  return "#ff517f"
                }
              },
              margin: 10,
              fontSize: 14,
              formatter: "{value}"
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show:
                options.xsplitLineShow === undefined
                  ? true
                  : options.xsplitLineShow,
              lineStyle: {
                color: options.xsplitLineColor
                  ? options.xsplitLineColor
                  : this.$STANDARD.splitLineColor //网格线颜色（所有图表需要统一）
              }
            }
          },
          {
            show: options.axisValueShow,
            type: "value",
            name: options.axis2Name ? options.axis2Name : "",
            nameTextStyle: {
              color: this.$STANDARD.xAxisColor,
              fontSize: 20
            },
            scale: options.isRightScale ? true : false,
            axisTick: {
              show: false
            },
            axisLabel: {
              color: this.$STANDARD.xAxisColor,
              //轴与标签的距离
              margin: 15,
              fontSize: 20,
              formatter: this.isRightAxis ? "{value}%" : "{value}"
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: this.$STANDARD.splitLineColor //网格线颜色（所有图表需要统一）
              }
            }
          }
        ];

        //类目轴数据
        for (let item of propData) {
          categoryData.push(item[options.xName]);
        }
        // x轴长度大于等于15 dataZoom 出现
        let dataZoomShow = categoryData.length >= 100 ? true : false;
        if (options.isVertical) {
          dataZoomShow = false;
        }
        //类目轴
        let axisCategory = [
          {
            type: "category", //类目轴
            data: categoryData,
            inverse: options.isVertical ? true : false,
            axisTick: {
              show: false
            },
            position: options.categoryInverse ? "right" : "left",
            triggerEvent: true,
            name: options.axisCategoryName ? options.axisCategoryName : "",
            nameLocation:
              options.isVertical && options.nameLocation ? "start" : "end",
            nameTextStyle: {
              color: this.$STANDARD.xAxisColor,
              fontSize: options.axisLabelSize ? options.axisLabelSize : 20
              // textBorderColor:"#16337e",
              // textBorderWidth:6,
              // textShadowBlur:40,
              // shadowOffsetX:30,
              // shadowOffsetY:30,
            },
            axisLabel: {
              rotate: options.rotate ? options.rotate : 0,
              // color: this.$STANDARD.xAxisColor,
              color: "#fefefe",

              fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
              lineHeight: 24,
              height: 30,
              // textBorderColor:"#16337e",
              // textBorderWidth:6,
              // textShadowBlur:40,
              // shadowOffsetX:30,
              // shadowOffsetY:30,
              // interval: 0,
              //轴与标签的距离
              margin: 15,
              formatter: params => {
                let res = "";
                let name = "";
                //label换行
                if (options.categoryBr) {
                  name = "";
                  let brNum = options.brNum ? options.brNum : 1;
                  for (let i = 0; i < params.length; i++) {
                    name += params[i];
                    if (i % brNum == brNum - 1) name += "\n";
                  }
                  res = name;
                } else {
                  res = params;
                }
                // return res;
                return options.islineHeight ? "{value|" + res + "}" : res;
              },
              rich: {
                value: {
                  lineHeight: 26,
                  fontSize: 20
                }
              }
            },
            //底线颜色
            axisLine: {
              show: options.axisLineShow ? true : false,
              lineStyle: {
                color: "#a1a1b4"
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: this.$STANDARD.splitLineColor
              }
            }
          },
          {
            show: false,
            type: "category", //类目轴
            data: categoryData
          }
        ];

        // 显示滚动轴过滤
        let dataZoom = [
          {
            show: dataZoomShow,
            type: "slider",
            backgroundColor: "rgba(70,235,255,0.1)",
            fillerColor: "rgba(70,235,255,0.3)",
            borderColor: "rgba(70,235,255,0.6)",
            xAxisIndex: [0, 1],

            filterMode: "filter",
            start: 0,
            end: 100,
            textStyle: {
              color: "#fff"
            },
            handleIcon:
              "M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z",
            handleSize: "80%",
            handleStyle: {
              color: "#fff",
              shadowBlur: 3,
              shadowColor: "rgba(0, 0, 0, 0.6)",
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }
        ];

        this.myChart = echarts.init(this.$refs.chartEl);
        this.option = {
          tooltip: {
            trigger: "axis",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            borderColor: this.$STANDARD.tooltipBorderColor,
            padding: 12,
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: options.tooltipType ? options.tooltipType : "shadow" // 默认为直线，可选为：'line' | 'shadow'
            },
            formatter: function (params) {
              let result = params[0].name + options.axisCategoryName + "<br/>";
              params.forEach(function (item, index) {
                let marker = '';
                if (typeof(options.colors.bar[index]) === 'string') {
                  marker = item.marker;
                } else {
                  marker = '<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:' + options.colors.bar[index][0] + ';"></span>'
                }
                result += `${marker}${item.seriesName} : ${item.value}%</br>`;
              });

              return result;
            }
          },
          legend: {
            show: options.legendShow,
            data: this.legendData,
            itemGap: options.legendGap ? options.legendGap : 10,
            top: options.legendPos ? options.legendPos.top : "3%",
            right: options.legendPos ? options.legendPos.right : "5%",
            itemWidth: options.legendW ? options.legendW : 25,
            itemHeight: options.legendH ? options.legendH : 14,
            icon:
              options.legendIcon === "undefined" ? "rect" : options.legendIcon,
            borderRadius: options.borderRadius ? options.borderRadius : 0,
            selected: this.params.legendActive ? this.params.legendActive : {},
            //   formatter: name => {
            //     let fix = "";
            //     fix = options.legendCut ? `{title|${name}}` : name;
            //     return fix;
            //   },
            textStyle: {
              fontSize: options.legendSize ? options.legendSize : 20,
              //跟随图标颜色
              // color: () => {
              //   return ''
              // },
              color: options.legendColor
                ? options.legendColor
                : this.$STANDARD.legendColor,
              rich: {
                title: {
                  width: 233,
                  fontSize: 20,
                  color: this.$STANDARD.legendColor
                }
              }
            }
          },
          grid: {
            left: options.left ? options.left : "5%",
            right: options.right ? options.right : "5%",
            top: options.top ? options.top : "5%",
            // bottom: options.bottom ? options.bottom : '5%',
            bottom: dataZoomShow ? "6%" : options.bottom ? options.bottom : "5%",
            containLabel: true
          },
          label: {
            show: true,
            position: options.isVertical ? "right" : "top",
            distance: 10,
            color: this.$STANDARD.xAxisColor,
            fontSize: 20,
            zlevel: 10
          },
          xAxis: options.isVertical ? axisValue : axisCategory,
          yAxis: options.isVertical ? axisCategory : axisValue,
          series: serirsArr,
          dataZoom: dataZoom,
          animation: this.animation,
          // animationEasing: "elasticOut",
          animationDelay: function (idx) {
            // 越往后的数据延迟越大
            return idx * 50;
          }
        };
        this.myChart.setOption(this.option);

        // 下钻页面
        this.myChart.on("click", params => {
          if (params.componentType === "series") {
            this.$emit("departmentClick", params.name);
          } else {
            this.$emit("departmentClick", params.value);
          }
        });
        this.legendselectchanged();
      },

      //边框bar legend 点击其背景图切换
      legendselectchanged() {
        this.myChart.on("legendselectchanged", params => {
          let options = this.params.options;
          let selected = params.selected;
          this.$emit("changeLegend", selected);

          if (options.mode === "overlap") {
            // 点击的legend name和边框bar name相等 && 其legend处于激活状态
            if (
              params.name === options.map[options.overlap] &&
              this.legendActive
            ) {
              this.legendActive = !this.legendActive;
              // this.imgSrc = require("@/assets/legendInactive.png");
              this.legendData.forEach((item, index) => {
                if (typeof item === "object") {
                  this.legendData[index].icon = `image://${
                    this.inActiveLegendBg
                    }`;
                }
              });
              this.myChart.setOption({
                legend: {
                  data: this.legendData
                }
              });
              // 点击的legend name和边框bar name相等 && 其legend处于未激活状态
            } else if (
              params.name === options.map[options.overlap] &&
              !this.legendActive
            ) {
              this.legendActive = !this.legendActive;
              //this.imgSrc = require("@/assets/legendActive.png");
              this.legendData.forEach((item, index) => {
                if (typeof item === "object") {
                  this.legendData[index].icon = `image://${this.activeLegendBg}`;
                }
              });
              this.myChart.setOption({
                legend: {
                  data: this.legendData
                }
              });
            }
          }
        });
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  };
</script>

